@tailwind base;
@tailwind components;
@tailwind utilities;

/* Base Reset Styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* Ensure portal root takes full height */
#portal-root {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

/* Brand colors - consider moving these to Tailwind config if used frequently */
.text-brand {
    color: #000080;
}

.bg-brand {
    background-color: #000080;
}

.border-brand {
    border-color: #000080;
}