.english-card-bg {
    background-image: url('../assets/eng_bg.jpg');
    background-size: cover;
    background-position: center;
}

.math-card-bg {
    background-image: url('../assets/eng_bg.jpg');
    background-size: cover;
    background-position: center;
}

/* Fallbacks for browsers that don't support webp */
@supports not (background-image: url('../assets/eng_bg.jpg')) {
    .english-card-bg {
        background-image: url('../assets/eng_bg.jpg');
    }

    .math-card-bg {
        background-image: url('../assets/math_bg.jpg');
    }
}